<template>
  <li @click="routerToPlay(item)">
    <img :src="item.image" alt="" />
    <p class="cated" v-if="item.cates[0]">{{item.cates[0].catename}}</p>
    <h3 class="title">{{ item.title }}</h3>
  </li>
</template>

<script>
export default {
  props: ["item"],
  methods: {
    routerToPlay(item) {
      // this.$store.commit("setCurrentPlay", this.item);
      this.$router.push({ path: "/play", query: { obj: item } });
    }
  }
};
</script>

<style lang="less">
li {
  position: relative;
  h3.title {
    position: absolute;
    width: 100%;
    bottom: 5px;
    left: 10px;
    font-size: 14px;
    font-style: normal;
    line-height: 2em;
    color: #fff;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-align: left;
    font-weight: bold;
    z-index: 5;
  }
  .cated{
    position: absolute;
    left:10px;
    bottom: 34px;
    font-size: 12px;
    color: #fff;
    z-index: 5;
  }
  &::after{
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.3);
  }
}
</style>
