<template>
  <div class="cate" v-if="cateList">
    <van-row>
      <van-col span="12" v-for="(item, index) in cateList" :key="index" @click="cateTo(item)">
        <img class="icon" :src="item.icon" alt="" />
        <h3 class="cates">#{{ item.catename }}#</h3>
      </van-col>
    </van-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      cateList: []
    };
  },
  methods: {
    getCateList() {
      this.axios
        .get(
          "http://api.kele8.cn/agent/https://app.vmovier.com/apiv3/cate/getList"
        )
        .then(response => {
          window.console.log(response);
          this.cateList = response.data.data;
        });
    },
    cateTo(item){
      this.$router.push({path:"/relatePlay",query:{obj:item}});
    }
  },
  created() {
    this.getCateList();
  }
};
</script>

<style lang="less">
.cate {
  .van-row {
    .van-col {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 180px;
      &::after{
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.3);
      }
      .icon {
        position: absolute;
        top: 0;
        left: 0;
      }
      .cates{
        color: #fff;
        position: absolute;
        z-index: 5;
      }
    }
  }
}
</style>
