<template>
  <div class="find" v-if="findData">
    <!-- 轮播图 -->

    <van-swipe
      v-if="findData.banner.list"
      class="my-swipe"
      :autoplay="3000"
      indicator-color="white"
    >
      <van-swipe-item
        v-for="(item, index) in findData.banner.list"
        :key="index"
      >
        <img :src="item.image" />
      </van-swipe-item>
    </van-swipe>

    <!-- today -->
    <Card :listData="findData.today"></Card>

    <!-- hot -->
    <Card :listData="findData.hot" :isSpecial="true"></Card>

    <!-- album -->
    <Card :listData="findData.album"></Card>

    <!-- yesterday -->
    <Card :listData="findData.posts"></Card>

    <button @click="getNext()">加载更多</button>
  </div>
  <van-skeleton v-else title :row="3" />
</template>

<script>
import Card from "@/components/Card.vue";
export default {
  components: {
    Card
  },
  data() {
    return {
      findData: null,
      historyPosts: []
    };
  },
  methods: {
    getFindData() {
      this.axios
        .get(
          "http://api.kele8.cn/agent/https://app.vmovier.com/apiv3/index/index"
        )
        .then(response => {
          window.console.log(response);
          ({ data: this.findData } = response.data);
        });
    },
    getNext() {
      var url =
        this.historyPosts.length == 0
          ? this.findData.posts.next_page_url_full
          : this.historyPosts[this.historyPosts.length - 1].next_page_url_full;
      this.axios.get("http://api.kele8.cn/agent/" + url).then(response => {
        window.console.log(response);
        this.historyPosts.push(response.data.data);
      });
    }
  },
  created() {
    this.getFindData();
  }
};
</script>

<style lang="less">
.find {
  .my-swipe {
    img {
      width: 100%;
    }
    .van-swipe__indicators {
      i {
        width: 10px;
        border-radius: 0;
        height: 3px;
      }
    }
  }
}
</style>
